import React, { useState, useEffect } from 'react';
import { notification } from 'antd';

const openNotification = () => {
  notification.open({
    message: <h3>Hey!</h3>,
    description:
      <p>This website is still under <b>heavy construction</b>. 
        Most pages might be inexistent or far form their final look. 
        In the meanwhile you can have a look at our <b>social pages</b>!</p>,
      placement: 'bottomRight',
      duration: 4.5,
  });
};


function App() {
  useEffect(() => {
    openNotification();
  });

  return (
    <>
      <div className="slide slide-first">
        <div className="centered">
          <h2>Welcome to</h2>
          <h1>Weird Frames</h1>
        </div>
      </div>
    </>
  );
}

export default App;
